.main {
    width: 100%;
    overflow: hidden;
    height: 400px;
    position: relative;
}

.list {
    /*	display: flex;*/
    width: 100%;
    height: 100%;
    /*	transition: 0.5s;*/
}

.item {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    transition-timing-function: linear;
    /*	flex-shrink: 0;*/
}

li {
    list-style: none;
    transition: 0.5s;
}

img {
    width: 100%;
    height: 100%;
}

* {
    margin: 0;
    padding: 0;
}

.prev,
.next {
    position: absolute;
    z-index: 10;
    width: 50px;
    height: 50px;
    background: #ccc;
    top: 0;
}

.prev {
    left: 0;
}

.next {
    right: 0;
}

.dots {
    text-align: center;
    position: absolute;
    bottom: 20px;
}

.dots a {
    display: inline-block;
    width: 10px;
    height: 10px;
    margin: 0 5px;
    background: #fff;
    border-radius: 50%;
    text-indent: -999em;
}

.dots a.active {
    background: #f60;
}


